import React, {useState, useEffect} from 'react'

export default function Password({updateAccess}) {

    const [val,
        setVal] = useState("")

    const [display,
        setDisplay] = useState("flex")

    const mdp = "Lacoste2021!"

    function handleChange(e) {
        setVal(e.target.value)
    }

    function handleSubmit(e) {
        e.preventDefault()
        if (val === mdp) {
            updateAccess(true);
            setDisplay('none');
        }
    }

    return (
        <div style={{
            display: display
        }} className="password z10">
            <form onSubmit={handleSubmit}>
                <input
                    type="password"
                    value={val}
                    onChange={handleChange}
                    placeholder="Type a password"/>
                <button type="submit">Valid</button>
            </form>
        </div>
    )
}