import React, {useRef, useState, useEffect} from 'react'
import useObserver from './useObserver'
import push1 from './medias/push1.jpg'
import push2 from './medias/push2.jpg'
import push3 from './medias/push3.jpg'

function PushItem({img}) {

    const [contentRef,
        visible] = useObserver({root: null, rootMargin: "0px", treshold: 0.8})

    return (
        <div ref={contentRef} className={`col-7 col-s-23 flex-col mgbt-s-2 hidden ${visible && "reveal"}`}>
            <div className="pushimg">
                <img className="cover" src={img} alt="img"/>
            </div>
            <a href="#" className="pushlink">View product</a>
        </div>
    )
}

export default function Push() {

    return (
        <div className="col-23 justify-between mgbt-2 wrap">
            <PushItem img={push1}/>
            <PushItem img={push2}/>
            <PushItem img={push3}/>
        </div>
    )

}
